<script>
import { ref } from 'vue'
import * as htmlToImage from 'html-to-image';

export default {
  setup() {
    const input = ref('');
    const color1 = ref('#003181')
    const color2 = ref('#2074D5')
    const color3 = ref('#003181')
    const textcolor = ref('black')

    const onSubmit = () => {
      
      var node = document.getElementById('logo');
      
  htmlToImage.toSvg(node)
  .then(function (dataUrl) {
    var link = document.createElement('a');
    link.download = 'my-cloud-cloud-logo.svg';
    link.href = dataUrl;
    link.click();
  });
        
    }

    return {
      input,
      color1,
      color2,
      color3,
      textcolor,
      onSubmit
    }
  }
}
</script>

<style>
.club-name{
    color: v-bind('textcolor');
}
.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
h1, #logo {
text-align: center;
}

form div {
  margin-top: 1rem;
}

label,
input,
button{
  font-size: 1rem;
  line-height: 1.2
}

form {
  width: 30em;
  max-width: 90%;
  margin: 0 auto;
}

label {
  margin-bottom: 0.4rem;
  display: block;
